<template>
  <div class="ui container">

    <div id="insertModal" class="ui fluid modal">
      <div class="content">
        <div style="margin: 20px">
          <form class="ui form">
            <h4 class="ui dividing header">Ajouter un nouveau enseignant : </h4>
            <br>
            <div class="fields">
              <div class="eight wide field">
                <label>Nom : </label>
                <input type="text"  v-model="nom"   placeholder="Nom du enseignant :">
              </div>
              <div class="six wide field">
                <label>Prenom : </label>
                  <input type="text"  v-model="prenom"   placeholder="Prenom du enseignant :">
              </div>
              <div class="six wide field">
                <label>Position occupée : </label>
                  <select  v-model="position" class="ui fluid dropdown">
                    <option selected disabled value="">Position :</option>
                    <option value="Chef de departement">Chef de departement</option>
                    <option value="Chef de filiere">Chef de filiere</option>
                    <option value="Responsable de module">Responsable de module</option>
                    <option value="Enseignant d'un element">Enseignant d'un element</option>
                  </select>
              </div>
            </div>

            <div class="field">
              <label>Numero du telephone : </label>
              <input type="text"  v-model="telephone"   placeholder="Numero du telephone d'enseignant :">
            </div>
            <div class="fields">
              <div class="eight wide field">
                <label>Email personnel: </label>
                  <input type="text"  v-model="emailPersonnel"   placeholder="Email personnel du enseignant">
              </div>
              <div class="eight wide field">
                <label>Email institutionnel: </label>
                  <input type="text"  v-model="emailInstitutionnel"   placeholder="Email institutionnel du enseignant">

              </div>
            </div>
          </form>
        </div>
        <br>
      </div>
      <div class="actions">
        <div class="ui medium primary icon button" @click="AddEnseignant">Enregistrer<i class="save icon"></i></div>
      </div>
    </div>

    <div id="updateModal" class="ui fluid modal">
      <div class="content">
        <div style="margin: 20px">
          <form class="ui form">
            <h4 class="ui dividing header">Modifier les informations personnel du enseignant : </h4>
            <br>
            <div class="fields">
              <div class="eight wide field">
                <label>Nom : </label>
                <input type="text"  v-model="current_post.nom"   placeholder="Nom du enseignant :">
              </div>
              <div class="six wide field">
                <label>Prenom : </label>
                <input type="text"  v-model="current_post.prenom"   placeholder="Prenom du enseignant :">
              </div>
              <div class="six wide field">
                <label>Position occupée : </label>
                <select  v-model="current_post.position" class="ui fluid dropdown">
                  <option selected disabled value="">Position :</option>
                  <option value="Chef de departement">Chef de departement</option>
                  <option value="Chef de filiere">Chef de filiere</option>
                  <option value="Responsable de module">Responsable de module</option>
                  <option value="Enseignant d'un element">Enseignant d'un element</option>
                </select>
              </div>
            </div>

            <div class="field">
              <label>Numero du telephone : </label>
              <input type="text"  v-model="current_post.telephone"   placeholder="Numero du telephone d'enseignant :">
            </div>
            <div class="fields">
              <div class="eight wide field">
                <label>Email personnel: </label>
                <input type="text"  v-model="current_post.emailPersonnel"   placeholder="Email personnel du enseignant">
              </div>
              <div class="eight wide field">
                <label>Email institutionnel: </label>
                <input type="text"  v-model="current_post.emailInstitutionnel"   placeholder="Email institutionnel du enseignant">

              </div>
            </div>
          </form>
        </div>
      </div>
      <div class="actions">
        <div class="ui positive button" @click="modifier(current_post)">Enregistrer !!</div>
      </div>
    </div>

    <div class="ui category search">
      <div class="ui icon input">
        <i class="search icon"></i>
        <input type="text" v-model="searchQuery" placeholder="Rechercher par nom " />
      </div>
    </div>

    <table class="ui selectable celled table">
        <thead>
        <tr>
          <th>Nom</th>
          <th>Prenom</th>
          <th>Telephone</th>
          <th>Email personnel</th>
          <th>Email institutionnel</th>
          <th>Position occupée</th>
          <th>Actions</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="row in resultQuery">
          <td>{{ row.nom }}</td>
          <td>{{ row.prenom }}</td>
          <td>{{ row.telephone }}</td>
          <td>{{ row.emailPersonnel }}</td>
          <td>{{ row.emailInstitutionnel }}</td>
          <td>{{ row.position }}</td>
          <td>
            <button class="compact circular ui medium icon positive button" @click="toggleUpdate(row)"><i class="edit icon"></i></button>
            <button class="compact circular ui medium icon negative  button" @click="supprimer(row)"><i class="trash icon"></i></button>
          </td>
        </tr>
        </tbody>
        <tfoot class="full-width">
        <tr>
          <th colspan="7">
            <div class="ui  primary labeled icon button" @click="toggleInsert">
              <i class="plus icon"></i> Ajouter un nouveau enseignant
            </div>
          </th>
        </tr>
        </tfoot>
      </table>

  </div>
</template>

<script>
	export default {
		name: "Home",
		components: {},

		data() {
			return {
			  current_post:{},
        searchQuery:'',
        nom: '',
        prenom:'',
        telephone: '',
        emailPersonnel:'',
        emailInstitutionnel:'',
        position:'',
				message: '',
        enseignants:[]
			}
		},
    computed: {
      resultQuery()
      {
        if (this.searchQuery) {
          return this.enseignants.filter((item) => {
            return this.searchQuery.toLowerCase().split(' ').every(v => item.nom.toLowerCase().includes(v))
          })
        } else {
          return this.enseignants;
        }
      }
    },
		methods: {
      supprimer(post) {
        this.$SHARED.dispatchAsyncDelete(this.$SHARED.services.SUPPRIMER_ENSEIGNANT,
            {id: post._id}).then(data => {
          this.message = data.message;
        }, () => {})
      },
      modifier(current_post){
        this.$SHARED.dispatchAsyncPut(this.$SHARED.services.MODIFIER_ENSEIGNANT,
            {
              _id: current_post._id,
              nom: current_post.nom,
              prenom:current_post.prenom,
              telephone:current_post.telephone,
              emailPersonnel:current_post.emailPersonnel,
              emailInstitutionnel:current_post.emailInstitutionnel,
              position:current_post.position
            }).then( data => {
          this.message = data.message;
        }, () => {})
      },

      toggleUpdate(post) {
        this.current_post = JSON.parse(JSON.stringify(post));
        $('#updateModal').modal('show')
      },
      toggleInsert() {
        $('#insertModal').modal('show')
      },
			AddEnseignant() {
				this.$SHARED.dispatchAsyncPost(this.$SHARED.services.AJOUTER_ENSEIGNANT,
            {nom: this.nom,prenom:this.prenom,telephone:this.telephone, emailPersonnel:this.emailPersonnel,
              emailInstitutionnel:this.emailInstitutionnel,position:this.position})
            .then( data => {
					this.message = data.message;
				}, () => {})
			},
      getALL(){
        console.log("Ajouter un departement");
        console.log(this.module)
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_ENSEIGNANTS ).then( data => {
          this.enseignants = data;
        }, () => {})
      }
    },
    mounted() {
      this.getALL();
		}
	}
</script>

